/* Headlines */
h1 {
  font-family: "Poppins", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 38px; }
  @media only screen and (min-width: 1024px) {
    h1 {
      font-size: 38px;
      line-height: 40px; } }

h2 {
  font-size: 32px;
  line-height: 34px;
  font-family: "Poppins", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: 700; }
  @media only screen and (min-width: 1024px) {
    h2 {
      font-size: 34px;
      line-height: 36px; } }

h3 {
  font-family: "Poppins", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 29px; }
  @media only screen and (min-width: 1024px) {
    h3 {
      font-size: 26px;
      line-height: 34px; } }

h4 {
  font-size: 18px;
  line-height: 20px;
  font-family: "Poppins", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: 700;
  margin-bottom: 30px; }
  @media only screen and (min-width: 1024px) {
    h4 {
      font-size: 24px;
      line-height: 26px; } }

h5 {
  font-size: 16px;
  line-height: 18px;
  font-family: "Poppins", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: 700;
  margin-bottom: 15px; }

/* Mixins for Heading */
/* Heading 1 */
h1 {
  color: #2d2f30; }

/* Heading 2 */
h2 {
  color: #2d2f30; }

/* Heading 3 */
h3 {
  color: #2d2f30; }

/* Heading 4 */
h4 {
  color: #2d2f30; }

/* Heading 5 */
h5 {
  color: #2d2f30; }

/* Heading Margins */
h2,
h3,
h4 {
  margin-bottom: 22px; }

/******************
     BODY TEXT
*******************/
.body--default-bold,
.text-bold, .body--default, body,
p,
ul,
ol {
  color: #383b41;
  font-family: "Roboto", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 18px; }

/* Classes for Body Text */
em,
i {
  font-style: italic; }

strong {
  font-weight: 700; }

p:not(:last-child) {
  margin-bottom: 30px; }

.purple {
  color: #8680ba; }

