/**
* Utility classes
*/
/**
* Wordpress image alignment
*/
.alignright {
  float: none; }
  @media screen and (min-width: 768px) {
    .alignright {
      float: right; } }

.alignleft {
  float: none; }
  @media screen and (min-width: 768px) {
    .alignleft {
      float: left; } }

.aligncenter {
  margin: 0 auto;
  max-width: 100%; }

.icon {
  display: inline-block;
  height: 1rem;
  width: 1rem; }

/**
* Site-wide layout styling
*/
html {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  position: relative; }

body {
  overflow-x: hidden; }

section {
  overflow: hidden;
  position: relative;
  width: 100%; }
  section::after {
    clear: both;
    content: "";
    display: block; }

.container {
  margin: 0 auto;
  max-width: 1440px;
  width: 100%; }
  .container::after {
    clear: both;
    content: "";
    display: block; }
  .container::after {
    content: '';
    display: none; }

input::-webkit-input-placeholder {
  line-height: normal !important; }

/**
* Site-wide styling
*/
.wrap,
.content-sidebar-wrap {
  margin: 0 auto;
  max-width: 90%;
  width: 100%; }
  @media only screen and (min-width: 1024px) {
    .wrap,
    .content-sidebar-wrap {
      max-width: 95%; } }

@media only screen and (min-width: 1024px) {
  .content-sidebar-wrap {
    display: flex;
    justify-content: space-between; } }

/* Header */
header.site-header {
  box-shadow: 0px -4px 13px #000; }

header .title-area {
  align-items: center;
  display: flex;
  justify-content: space-between;
  max-height: 100px; }
  @media only screen and (min-width: 1024px) {
    header .title-area {
      max-height: 60px; } }

/* Body Content*/
.site-inner {
  padding-top: 30px; }
  .site-inner .content {
    max-width: 60%;
    width: 100%; }
    .site-inner .content .entry-content {
      margin-top: 20px; }
  .site-inner .sidebar {
    max-width: 35%;
    width: 100%; }

/* Footer */
footer.site-footer {
  align-items: center;
  background: #000;
  bottom: 0;
  display: flex;
  height: 150px;
  margin-top: 30px;
  position: fixed;
  width: 100%; }
  footer.site-footer p {
    color: #fff; }
